
import { defineComponent } from "vue";
import Pagination from "@/shared/components/Pagination/Pagination.vue";
import { PAGE_SIZE } from "@/shared/constants/common.const";
import { DateFormat } from "@/shared/services/DateTimeService";
import { ICM21200111_RES, boardProductInfoInquiryList } from "@/shared/type/ICM21200111.type";
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import SelectOption from "@/shared/components/SelectOption/SelectOption.vue";

const dateFormatter = new DateFormat();

export default defineComponent({
  name: "icm212001sc00",
  components: {
    Pagination,
    BreadCrumb,
    SelectOption
  },

  data() {
    const productsList = [] as boardProductInfoInquiryList[];

    const searchFields = {
      searchTypeCode: "",
      searchValue: "",
      displayYN: "",
      statusCode: ''
    };

    const headers = [
      { columnName: "No." },
      { columnName: "View" },
      { columnName: "Product Category Code" },
      { columnName: "Product Category Name" },
      { columnName: "Product Code" },
      { columnName: "Product Name" },
      { columnName: "Status" },
      { columnName: "Created Date" },
      { columnName: "Display" },
    ];

    return {
      productsList,
      searchFields,
      headers,
      totalItems: 0,
      currentPage: 1,
      pageSize: PAGE_SIZE,
      startingIndex: 1,
      resetPage: false,
    };
  },

  created() {
    this.requestProductList();
  },

  computed: {
    displayYN() {
      return this.$store.state.CommonCodeModule.commonCode.YnType;
    },
  },

  methods: {
    async requestProductList(mode?: string) {
      const body = {
        displayYN: this.searchFields.displayYN,
        searchTypeCode: this.searchFields.searchTypeCode,
        searchValue: this.searchFields.searchValue,
        statusCode: this.searchFields.statusCode,
        pageSize: this.pageSize,
        pageNumber: mode == 'search' ? 0 : this.currentPage,
      };

      if (mode === 'search') {
        this.currentPage = 1
        this.resetPage = true;
      }

      const res = (await this.$request.getRequest("ICM21200111", body, false)) as ICM21200111_RES;

      if (res?.header?.result) {
        this.productsList = res.body.list.map(
          (data: boardProductInfoInquiryList) => {
            return {
              productName: data.productName,
              displayYN: this.$codeConverter.convertValue("YnType", data.displayYN),
              productCode: data.productCode,
              categoryName: data.categoryName,
              boardCategoryCode: data.boardCategoryCode,
              firstRegisterDate: dateFormatter.formatStringToStringDate(data.firstRegisterDate),
              statusCode: `${data.statusCode} : ${this.$codeConverter.convertValue("ContentManangementStatusCode", data.statusCode)}`,
            };
          }
        );
      }

      this.totalItems = res.body.totalCount;
    },

    handleClickAction() {
      this.$router.push({
        path: `/ICM212001SC01`,
        query: {
          type: "register",
        },
      });
    },

    resetForm() {
      for (const key in this.searchFields) {
        this.searchFields[key] = "";
      }
      this.searchFields.statusCode = ''
      this.resetPage = true;
      this.currentPage = 1

      this.requestProductList();
    },

    handleRowClick(item: boardProductInfoInquiryList) {
      this.$router.push({
        path: `/ICM212001SC01`,
        query: {
          type: "edit",
          productCode: item.productCode,
          productCategoryCode: item.boardCategoryCode,
        },
      });
    },

    handlePagination(pageNumber: number) {
      this.currentPage = pageNumber;
      this.resetPage = false;
      this.requestProductList();
    },

    handleStartingIndex(startingIndex: number) {
      this.startingIndex = startingIndex;
    },
  },
});
